import isCreditCard from 'validator/lib/isCreditCard';

export default (values) => {
  let errors = {};
  const { cardNumber, cardHolder, cardMonth, cardYear, cardCVV } = values;

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –Ω–æ–º–µ—Ä–∞ –∫–∞—Ä—Ç—ã
  if (!cardNumber) {
    errors.cardNumber = '–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –Ω–æ–º–µ—Ä –∫–∞—Ä—Ç—ã';
  } else if (!isCreditCard(cardNumber)) {
    errors.cardNumber = '–ù–µ–≤–µ—Ä–Ω—ã–π –Ω–æ–º–µ—Ä –∫–∞—Ä—Ç—ã';
  }

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –§–ò–û
  const MIN_NAME_LENGTH = 3;
  const trimmedCardHolder = cardHolder.trim();

  if (!trimmedCardHolder) {
    errors.cardHolder = '–í–≤–µ–¥–∏—Ç–µ –§–∞–º–∏–ª–∏—é –∏ –ò–º—è';
  } else if (trimmedCardHolder.length < MIN_NAME_LENGTH) {
    errors.cardHolder = `–ù–µ –∫–æ—Ä–æ—á–µ ${MIN_NAME_LENGTH} —Å–∏–º–≤–æ–ª–æ–≤`;
  }

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –º–µ—Å—è—Ü–∞
  const MAX_MONTH_VALID = 12;

  if (!cardMonth) {
    errors.cardMonth = '–í–≤–µ–¥–∏—Ç–µ –º–µ—Å—è—Ü';
  } else if (parseInt(cardMonth, 10) > MAX_MONTH_VALID) {
    errors.cardMonth = '–ù–µ–≤–µ—Ä–Ω–æ —É–∫–∞–∑–∞–Ω –º–µ—Å—è—Ü';
  }

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –≥–æ–¥–∞
  const MAX_YEAR_VALID = 18; // –Ω—É–∂–Ω–æ —Ñ–µ—Ç—á–∏—Ç—å –¥–∞—Ç—É —Å –±–µ–∫–∞, –≤ –±—Ä–∞—É–∑–µ—Ä–µ –º–æ–∂–µ—Ç –±—ã—Ç—å —Å–±–∏—Ç–∞—è –¥–∞—Ç–∞

  if (!cardYear) {
    errors.cardYear = '–í–≤–µ–¥–∏—Ç–µ –≥–æ–¥';
  } else if (parseInt(cardYear, 10) < MAX_YEAR_VALID) {
    errors.cardYear = '–ö–∞—Ä—Ç–∞ —É–∂–µ –∏—Å—Ç–µ–∫–ª–∞';
  }

  // –í–∞–ª–∏–¥–∞—Ü–∏—è CVV
  if (!cardCVV) {
    errors.cardCVV = '–í–≤–µ–¥–∏—Ç–µ CVV –∫–æ–¥';
  } else if (cardCVV.length < 3) {
    errors.cardCVV = '–í–≤–µ–¥–∏—Ç–µ –∫–æ–¥ –ø–æ–ª–Ω–æ—Å—Ç—å—é';
  }

  return errors;
};
