import VMasker from 'vanilla-masker';

export default {
  cardNumber: (value) => VMasker.toPattern(value, '9999 9999 9999 9999'),
  cardHolder: (value) =>
    value
      .trimStart()
      .toUpperCase()
      .replace(/\d/g, '') // —É–±–∏—Ä–∞–µ–º —Ü–∏—Ñ—Ä—ã
      .replace(/[^A-Z\s]+/g, '') // —É–±–∏—Ä–∞–µ–º –ù–ï –ª–∞—Ç–∏–Ω–∏—Ü—É
      .replace(/\s{2,}/g, ' ') // –∑–∞–º–µ–Ω—è–µ–º –¥–≤–∞ –ø—Ä–æ–±–µ–ª–∞ –Ω–∞ –æ–¥–∏–Ω
      .substring(0, 20), // —Ç–æ–ª—å–∫–æ –ø–µ—Ä–≤—ã–µ 20 —Å–∏–º–≤–æ–ª–æ–≤
  cardMonth: (value) => VMasker.toPattern(value, '99'),
  cardYear: (value) => VMasker.toPattern(value, '99'),
  cardCVV: (value) => VMasker.toPattern(value, '999'),
};
